import 'chartjs-plugin-piechart-outlabels'

async function test() {
	const expenses = document.querySelector('.expenses__adding'),
		goodsNames = document.querySelectorAll('.expenses__adding button'),
		dateELEM = document.querySelector('.expenses__adding-date'),
		submitAdding = document.querySelector('.expenses__adding-form button'),
		d = new Date(),
		dateString = d.getDate() + '-' + (d.getMonth() + 1) + '-' + d.getFullYear(),
		addExpensesSubmit = document.querySelector('.expenses__adding-addMenuItem'),
		addExpensesForm = document.querySelector('.expenses__adding-new'),
		addExpensesNewName = addExpensesForm.querySelector('input')

	dateELEM.innerHTML = dateString

	let backElemColor = [],
		spendingName = []

	let objToLocal = {}

	addExpensesForm.addEventListener('submit', function (e) {
		e.preventDefault()

		objToLocal.name = new FormData(e.currentTarget).get('name')
		init()
	})

	const labels = document.querySelectorAll('.expenses__adding-elem label')
	labels.forEach(item => {
		const tamColor = dynamicColors()
		item.style.color = tamColor
		backElemColor.push(tamColor)
		spendingName.push(item.textContent.toLocaleUpperCase())
	})

	function initSrazu() {
		const initialElemFromLocal = localStrObj.read()
		if (initialElemFromLocal.length > 0) {
			initialElemFromLocal.forEach(item => {
				init(item)
				console.log(item)
			})
		}
	}

	const isEmpty = str => !str.trim().length

	function init(localObj) {
		const oneElemForm = document.createElement('form')
		oneElemForm.classList.add('expenses__adding-form')
		const elemInsideForm = document.createElement('div')
		elemInsideForm.classList.add('expenses__adding-elem')
		const label = document.createElement('label')
		console.log(addExpensesNewName.value + ' 4e tam')

		// label.textContent = `${addExpensesNewName.value ?? localObj.name}`
		label.textContent = isEmpty(addExpensesNewName.value) ? localObj.name : addExpensesNewName.value
		label.style.color = dynamicColors()
		label.setAttribute('for', `${addExpensesNewName.value ?? localObj.name}`)

		const expenseCost = document.createElement('input')
		expenseCost.placeholder = 'сумма'
		expenseCost.type = 'text'
		expenseCost.required = true
		// expenseCost.name = `${addExpensesNewName.value ?? '0'}`
		expenseCost.name = 'cost'
		expenseCost.classList.add('expenses__adding-elem_name')
		// expenseCost.setAttribute('id', `${addExpensesNewName.value ?? localObj.id}`)

		const expenseDescr = document.createElement('input')
		expenseDescr.classList.add('expenses__adding-elem_descr')
		expenseDescr.type = 'text'
		expenseDescr.name = `descr`
		expenseDescr.placeholder = 'описание'
		expenseDescr.setAttribute('id', `descr${addExpensesNewName.value}`)

		const addNewExpenseBtn = document.createElement('button')
		addNewExpenseBtn.textContent = 'Добавить к расходам'
		addNewExpenseBtn.classList.add('btn')
		addNewExpenseBtn.type = 'submit'

		const totalOfOne = document.createElement('div')
		totalOfOne.classList.add('expenses__adding-form_total')
		totalOfOne.innerHTML = `Итого:&nbsp;&nbsp;&nbsp;<span>${expenseCost.value ?? localObj.total}</span>`

		const delExpense = document.createElement('button')
		delExpense.textContent = 'Удалить позицию'
		delExpense.classList.add('btn')

		oneElemForm.append(elemInsideForm)
		elemInsideForm.append(delExpense)
		elemInsideForm.append(label)
		elemInsideForm.append(expenseCost)
		elemInsideForm.append(expenseDescr)
		elemInsideForm.append(addNewExpenseBtn)
		elemInsideForm.append(totalOfOne)
		expenses.append(oneElemForm)

		oneElemForm.addEventListener('submit', function (e) {
			e.preventDefault()
			const thisForm = e.currentTarget
			const total = thisForm.querySelector('.expenses__adding-form_total span')

			const formDate = new FormData(thisForm)
			formDate.forEach((value, key) => {
				objToLocal[key] = value
			})

			// objToLocal.id = Object.keys(formObj)[0]
			objToLocal.date = dateString
			let tmpValue = 0
			const allElem = localStrObj.read()
			const sortedElems = allElem.filter(elem => {
				return objToLocal.name === elem.name
			})
			if (sortedElems.length > 0) {
				tmpValue = Number(sortedElems[sortedElems.length - 1].total)
			}

			total.textContent = tmpValue + Number(objToLocal.cost ?? '0')
			objToLocal.total = Number(total.textContent)
			localStrObj.create(objToLocal)
			thisForm.reset()
		})
	}

	// let spendingCost = [1, 1, 1, 1, 1],
	let spendingCost = [1]
	// da = document.querySelector('.da')
	// da.addEventListener('click', function (e) {
	// 	spendingName.push('NEW')
	// 	spendingCost.push(120)
	// 	backElemColor.push(dynamicColors())
	// 	chart.update()
	// })

	let chart = new Chart('outlabeledChart', {
		type: 'outlabeledPie',
		data: {
			labels: spendingName.length > 0 ? spendingName : ['Nothing'],
			datasets: [
				{
					backgroundColor: backElemColor,
					data: spendingCost
				}
			]
		},
		options: {
			plugins: {
				legend: false,
				outlabels: {
					text: '%l %p',
					color: 'white',
					stretch: 45,
					font: {
						resizable: true,
						minSize: 12,
						maxSize: 18
					}
				}
			}
		}
	})

	function dynamicColors() {
		let r = Math.floor(Math.random() * 255)
		let g = Math.floor(Math.random() * 255)
		let b = Math.floor(Math.random() * 255)
		return 'rgb(' + r + ',' + g + ',' + b + ')'
	}

	class LocalStorageManager {
		#localStorageKey = 'notice'

		create(noticeDataObj) {
			const noticeFromLocal = localStorage.getItem(`${this.#localStorageKey}`) ?? '[]'

			const parseAndAddNewOne = JSON.parse(noticeFromLocal)
			parseAndAddNewOne.push(noticeDataObj)
			localStorage.setItem(`${this.#localStorageKey}`, JSON.stringify(parseAndAddNewOne))
		}
		read() {
			const noticeFromLocal = localStorage.getItem(`${this.#localStorageKey}`) ?? '[]'
			return JSON.parse(noticeFromLocal)
		}
		update(noticeDataObj) {
			const noticeFromLocal = localStorage.getItem(`${this.#localStorageKey}`) ?? '[]'

			const noDuplicateObjects = JSON.parse(noticeFromLocal)
			noDuplicateObjects.forEach(localNoticeObj => {
				if (localNoticeObj.elemId == noticeDataObj.timeCreating) {
					localNoticeObj.value = noticeDataObj.value
				}
			})

			localStorage.setItem(`${this.#localStorageKey}`, JSON.stringify(noDuplicateObjects))
		}

		delete(noticeDataObj) {
			const noticeFromLocal = localStorage.getItem(`${this.#localStorageKey}`) ?? '[]'

			const localAnswer = JSON.parse(noticeFromLocal)
			const withoutDeleted = localAnswer.filter(item => {
				console.log(typeof noticeDataObj.timeCreating)
				console.log(typeof item.elemId)
				return toString(noticeDataObj.timeCreating) !== toString(item.elemId)
			})

			withoutDeleted.forEach(item => {
				console.log(item)
			})
			localStorage.setItem(`${this.#localStorageKey}`, JSON.stringify(withoutDeleted))
		}
	}
	const localStrObj = new LocalStorageManager()
	initSrazu()
}
export default test
